.nav {
	.nav-item {
		.nav-link {
			&.active {
				color: #0ea5e9
			}
		}
	}
}
.nav.nav-outline {
	.nav-item {
        .nav-link {
          background-color: transparent;
        }
		.nav-link:not(.disabled) {
            border: 1px solid transparent;
            border-radius: $border-radius;
            &.active {
                border-color: #e5e7eb;
                color: #1f2937;
            }
        }
	}
	&.flex-column {
		.nav-item {
			&:not(:first-child) {
				margin-left: 6px;
			}
		}
	}
}
.nav.nav-pills-secondary {
  .nav-item {
        .nav-link:not(.disabled) {
            border: 1px solid transparent;
            border-radius: $border-radius;
            &:not(.active).focus {
                background-color: #f3f4f6;
                border-color: #f3f4f6;
            }
            &.active {
                border-color: #e9e9ea;
                background-color: #e9e9ea;
                color: #1f2937;
            }
        }
    }
}
.nav.nav-pills-primary {
    .nav-item {
		.nav-link {
			background-color: transparent;
		}
        .nav-link:not(.disabled) {
            border: 1px solid transparent;
            border-radius: $border-radius;
            &.active {
                border-color: #0ea5e9;
                background-color: #0ea5e9;
                color: #fff;
            }
        }
    }
}
.nav-line-tab {
	.nav-link {
		border-bottom: 2px solid transparent;
		&:hover {
			color: #0ea5e9;
		}
		&.active {
			border-bottom-color: #0ea5e9;
		}
	}
}

.nav.nav-outline-primary {
	.nav-item:not(:first-child) {
		margin-left: -1px;
	}
	.nav-item {
		&:first-child {
			.nav-link {
				border-radius: $border-radius 0 0 $border-radius;
			}
		}
		&:last-child {
			.nav-link {
				border-radius: 0 $border-radius $border-radius 0;
			}
		}
		.nav-link {
			border: 1px solid $list-group-border-color;
			background-color: transparent;
			.nav-title {
				color: inherit;
				font-size: 1rem;
				font-weight: 500;
			}
			.nav-desc {
				font-weight: 400;
				color: $text-muted;
				font-size: 0.875rem;
			}
		}
	}
}
.nav.nav-outline-primary.flex-column {
	@include nav_outline_flex_column;
}
@media (min-width: 576px) {
	.nav.nav-outline-primary.flex-sm-column {
		@include nav_outline_flex_column;
	}
}
@media (min-width: 768px) {
	.nav.nav-outline-primary.flex-md-column {
		@include nav_outline_flex_column;
	}
}
@media (min-width: 992px) {
	.nav.nav-outline-primary.flex-lg-column {
		@include nav_outline_flex_column;
	}
}
@media (min-width: 1200px) {
	.nav.nav-outline-primary.flex-xl-column {
		@include nav_outline_flex_column;
	}
}
@media (min-width: 1400px) {
	.nav.nav-outline-primary.flex-xxl-column {
		@include nav_outline_flex_column;
	}
}
