.page-link {
    border: $pagination-border-width solid transparent;
    border-radius: $border-radius;
    font-size: 0.9rem;
    font-weight: 500;
    line-height: 1.5rem;
}

.page-item {
    &:first-child {
        margin-right: auto;
        .page-link {
            text-transform: uppercase;
            background-color:#f3f4f6;
            border-color: #f3f4f6;
            border-radius: $border-radius;
            &:not(:disabled):not(.disabled):hover {
                background-color: #e5e7eb;
                border-color: #e5e7eb;
            }
        }
    }
    &:last-child {
        margin-left: auto;
        .page-link {
            text-transform: uppercase;
            background-color: #f3f4f6;
            border-color: #f3f4f6;
            border-radius: $border-radius;
            &:not(:disabled):not(.disabled):hover {
                background-color: #e5e7eb;
                border-color: #e5e7eb;
            }
        }
    }
    &:not(:first-child) .page-link {
        margin-left: $pagination-margin-start;
    }
    &:not(:first-child):not(:last-child) {
        margin: 0 4px;
    }
}

.pagination-outline {
    .page-item {
        &:not(:first-child):not(:last-child) .page-link {
            font-weight: 400;
            &:hover {
                z-index: 2;
                color: #212529;
                background-color: #fff;
                border-color: #eaecef;
            }
            &:focus {
                z-index: 2;
                color: #212529;
                background-color: #fff;
                border-color: #eaecef;
            }
        }
        &.active .page-link {
            z-index: 3;
            color: #212529;
            font-weight: 500!important;
            background-color: #fff;
            border-color: #eaecef;
        }
    }
}

.pagination-custom {
    .page-item {
        padding: 0 0 20px;
        border-bottom: 2px solid transparent;
        &:not(:first-child):not(:last-child) .page-link {
            background-color: #f3f4f6;
            border-color: #f3f4f6;
            &:hover {
                z-index: 2;
                background-color: #e5e7eb;
                border-color: #e5e7eb;
            }
            &:focus {
                z-index: 2;
                background-color: #e5e7eb;
                border-color: #e5e7eb;
            }
        }
        &.active {
            border-color: #0ea5e9;
            .page-link {
                z-index: 3;
                color: #0ea5e9;
                background-color: #e5e7eb!important;
                border-color: #e5e7eb!important;
            }
        }
    }
}
