.list-group.list-group-separate {
	.list-group-item {
		margin-bottom: 6px;
		border: 1px solid $list-group-border-color;
		border-radius: $list-group-border-radius;
	}
}
@each $color, $value in $theme-colors {
	.list-group.list-group-light-#{$color} {
		.list-group-item {
			border: 0!important;
			background-color: tint-color($value, 98%);
			&:hover {
				background-color: tint-color($value, 95%);
				border-color: tint-color($value, 95%);
			}
			&.active, &:active, &:focus {
				background-color: tint-color($value, 95%);
				border-color: tint-color($value, 95%);
				color: #1f2937;
				border: 0;
				margin-top: 0;
			}
		}
	}
}
