// The dropdown menu
.dropdown-menu {
	box-shadow: 0 0 50px 0 rgb(82 63 105 / 15%);
}
@media only screen and (max-width: 576px) {
	.dropdown-menu {
		position: fixed!important;
		transform: none!important;
		width: 100%!important;
		border-radius: 0!important;
		left: 0!important;
		top: 56px!important;
		bottom: 0!important;
	}
}
