.timeline {
    .timeline-item {
        position: relative;
        padding: 0;
        margin: 0;
        display: flex;
        align-items: flex-start;
        width: 100%;
        .timeline-line {
            display: block;
            content: " ";
            justify-content: center;
            position: absolute;
            z-index: 0;
            left: 0;
            top: 0;
            bottom: 0;
            transform: translate(50%);
            border-left-width: 1px;
            border-left-style: solid;
            border-left-color: #e4e6ef;
            width: 34px;
        }
        .timeline-icon {
            align-items: center;
            background-color: #f5f8fa;
            background-position: 50%;
            background-repeat: no-repeat;
            background-size: cover;
            border-radius: 50%;
            color: #3f4254;
            display: flex;
            flex-shrink: 0;
            font-weight: 500;
            height: 34px;
            justify-content: center;
            margin-right: 1rem;
            width: 34px;
            z-index: 1;
            border: 2px solid #fff;
            svg {
                height: 16px;
                width: 16px;
            }
        }
        .timeline-content {
            width: 100%;
            margin-bottom: 30px;
        }
    }
}
