.pre-loader {
    height: 100%;
    left: 0;
    outline: 0;
    overflow-x: hidden;
    overflow-y: auto;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 99999;
    transition: display .5s ease-out;
}
.pre-loader-container {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $white;
    transition: display .5s ease-out;
}
.pre-loader-icon {
    width: 84px;
    height: 84px;
    margin: 50px auto;
    transform: rotate(45deg);
    position: relative;
    animation: animate 1.2s infinite ease-in-out;
}
.pre-loader-icon span{
    background-color: #38bdf8;
    width: 40px;
    height: 40px;
    border: 1px #38bdf8 solid;
    border-radius: 5px;
    display: block;
    position: absolute;
    animation: animate_1 1.2s infinite ease-in-out both;
}
.pre-loader-icon span:nth-child(1){
    top: 0;
    left: 0;
}
.pre-loader-icon span:nth-child(2){
    background-color: #0ea5e9;
    border: 1px #0ea5e9 solid;
    top: 0;
    right: 0;
    animation: animate_2 1.2s infinite ease-in-out both;
}
.pre-loader-icon span:nth-child(3){
    background-color: #0ea5e9;
    border: 1px #0ea5e9 solid;
    bottom: 0;
    left: 0;
    animation: animate_2 1.2s infinite ease-in-out both;
}
.pre-loader-icon span:nth-child(4){
    background-color: #38bdf8;
    border: 1px #38bdf8 solid;
    bottom: 0;
    right: 0;
}
@keyframes animate{
    0%, 10%, 100%{
        width: 84px;
        height: 84px;
    }
    65%{
        width: 150px;
        height: 150px;
    }
}
@keyframes  animate_1{
    0%, 30%{ transform: rotate(0); }
    55%{
        background-color: transparent;
        transform: scale(0.5);
    }
    100%{ transform: rotate(180deg); }
}
@keyframes  animate_2{
    0%, 30%{ transform: rotate(0); }
    55%{
        background-color: transparent;
        transform: scale(0.5);
    }
    100%{ transform: rotate(180deg); }
}
