@each $color, $value in $theme-colors {
  	.btn-light-#{$color} {
	  $colorValue: tint-color($value, 90%);
		@include button-variant($colorValue, $colorValue);
		color: $value;
		&:hover {
			background-color: tint-color($value, 80%);
			border-color: tint-color($value, 80%);
			color: $value;
		}
		&:focus, &:active {
    		background-color: tint-color($value, 80%);
			border-color: tint-color($value, 80%);
			color: $value;
  		}
  	}
}
.btn-danger, .btn-primary, .btn-success, .btn-info, .btn-warning, .btn-secondary, .btn-dark {
  	color: $white;
	&:hover, &:active, &:focus {
		color: $white;
	}
	&.disabled {
		color: $white;
	}
}
.btn-outline-danger, .btn-outline-primary, .btn-outline-success, .btn-outline-info, .btn-outline-warning {
	&:hover {
		color: $white;
	}
}
.btn-progress {
    position: relative;
    @include btn_spinner(36px);
    color: transparent !important;
    pointer-events: none;
}
.buttons .btn {
	margin: 0 20px 20px 0;
}
.btn-outline {
	border-color: #e4e6ef;
}
