.selectgroup {
    display: flex;
    flex-direction: column;
    .selectgroup-item {
        cursor: pointer;
        &:not(:last-child) {
            margin-bottom: 1rem;
        }
        .selectgroup-input {
            opacity: 0;
            position: absolute;
            z-index: -1;
            top: 0;
            left: 0;
        }
        .selectgroup-button {
            display: flex;
            align-items: flex-start;
            border: 0.15rem solid #e5e7eb;
            border-radius: 0.675rem;
            padding: 1.2rem;
            transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
            .selectgroup-button-icon {
                width: 2.5rem;
                height: 2.5rem;
                font-size: 1.2rem;
                border-radius: 50%;
                background-color: #f3f4f6;
                color: #6b7280;
                display: flex;
                justify-content: center;
                align-items: center;
                margin-right: 1rem;
            }
            .selectgroup-label {
                display: flex;
                flex-direction: column;
                flex: 1;
                .selectgroup-title {
                    font-size: 1rem;
                    font-weight: 500;
                }
                .selectgroup-desc {
                    color: #6b7280;
                    font-size: 0.95rem;
                }
            }
            .selectgroup-button-check {
                width: 1.8rem;
                height: 1.8rem;
                opacity: 0;
                margin-left: 1rem;
                @include check_icon(1.5rem, #38bdf8);
            }
            &:hover {
                border-color: #38bdf8;
            }
        }
    }
    .selectgroup-input:checked + .selectgroup-button {
        border-color: #38bdf8;
        z-index: 1;
        .selectgroup-button-icon {
            color: #38bdf8;
        }
        .selectgroup-label {
            .selectgroup-title {
                color: $primary;
            }
        }
        .selectgroup-button-check {
            opacity: 1;
        }
    }
}
