.table {
	> thead {
		vertical-align: bottom;
		th {
		vertical-align: bottom;
		border-bottom: 0;
		text-transform: capitalize;
		line-height: 2;
		font-size: .9rem;
		opacity: .8;
		color: #6b7280;
		background-color: #f3f4f6;
		}
	}
}
.table-borderless {
  // stylelint-disable-next-line selector-max-universal
  > :not(caption) > * > * {
    border-bottom-width: 0;
  }

  > :not(:first-child) {
    border-top-width: 0;
  }
}

.table-row-bordered {
	> tbody > tr:not(:last-child) > td {
		border-bottom: 1px dashed tint-color(#e5e7eb, 50%);
	}
}
.table-text-center {
	th, td {
		vertical-align: middle!important;
	}
}
