html {
    font-feature-settings: "cv02","cv03","cv04","cv11";
}
ul {
    margin: 0;
    padding: 0;
}
::-webkit-scrollbar-thumb {
    border-radius: 0.1rem;
    background-color: #e5e7eb
}
::-webkit-scrollbar-corner {
    background-color: #e2e8f0
}
::-webkit-scrollbar-button {
    display: none
}
::-webkit-scrollbar {
    width: 0.6rem;
    height: calc(.4rem + 0.4rem)
}
.hover-scroll-overlay-y {
    overflow-y: hidden;
    position: relative;
    --scrollbar-space: 0.1rem;
    transition: background-color .3s ease-in-out;
}
.hover-scroll-overlay-y::-webkit-scrollbar {
    width: calc(.4rem + var(--scrollbar-space));
    border-radius: 0.1rem;
}
.hover-scroll-overlay-y::-webkit-scrollbar-thumb {
    background-clip: content-box;
    border-right: var(--scrollbar-space) solid transparent
}
.hover-scroll-overlay-y:hover {
    overflow-y: overlay;
}
.hover-scroll-overlay-y:hover::-webkit-scrollbar-thumb {
    background-color: #e5e7eb
}
@-moz-document url-prefix() {
    .hover-scroll-overlay-y {
        overflow-y: scroll;
        position: relative;
    }
}
.side-nav .hover-scroll-overlay-y {
    --scrollbar-space: 0.4rem;
    scrollbar-color: #475569 transparent;
}
.side-nav .hover-scroll-overlay-y::-webkit-scrollbar-thumb {
    background-color: #475569;
}
.side-nav .hover-scroll-overlay-y:hover {
    scrollbar-color: #334155 transparent;
}
.side-nav .hover-scroll-overlay-y:hover::-webkit-scrollbar-thumb {
    background-color: #334155;
}
.fs-base {
    font-size: 1rem !important
}
.fs-fluid {
    font-size: 100% !important
}
.line-clamp-1 {
    @include line_clamp();
}
.line-clamp-2 {
    @include line_clamp(2);
}
.line-clamp-3 {
    @include line_clamp(3);
}
.line-clamp-4 {
    @include line_clamp(4);
}
.line-clamp-5 {
    @include line_clamp(5);
}
.line-clamp-6 {
    @include line_clamp(6);
}
@keyframes dropdown-animation-fade-in {
    from {
        opacity: 0
    }
    to {
        opacity: 1
    }
}
@keyframes dropdown-animation-move-up {
    from {
        margin-top: .75rem
    }
    to {
        margin-top: 0
    }
}
@keyframes animation-drawer-fade-in {
    from {
        opacity: 0
    }
    to {
        opacity: 1
    }
}
