.bg-svg-user {
    @include svg_background("/src/img/user.svg");
}
.bg-svg-truck {
    @include svg_background("/src/img/truck.svg");
}
.bg-svg-dollar {
    @include svg_background("/src/img/dollar-sign.svg");
}
.user-image {
    width: 2.6rem;
    height: 2.6rem;
    border-radius: 0.5rem;
}
.text-muted-hover:hover {
    color: get-palette-color("slate", 300)!important;
}
.text-primary-hover:hover {
    color: get-palette-color("sky", 600)!important;
}
.bw-2 {
    border-width: 2px!important;
}
.border-end-1 {
    border-right: 1px solid $border-color;
}
.rounded-te-3 {
    border-top-right-radius: .5rem!important;
}
.rounded-ts-3 {
    border-top-left-radius: .5rem!important;
}
.rounded-bs-3 {
    border-bottom-left-radius: .5rem!important;
}
.rounded-be-3 {
    border-bottom-right-radius: .5rem!important;
}
.rounded-te-0 {
    border-top-right-radius: 0!important;
}
.rounded-ts-0 {
    border-top-left-radius: 0!important;
}
.rounded-bs-0 {
    border-bottom-left-radius: 0!important;
}
.rounded-be-0 {
    border-bottom-right-radius: 0!important;
}
@each $name, $color in $theme-colors {
    .bg-light-#{$name} {
        background-color: tint-color($color, 90%)!important;
    }
    .bg-#{$name}-100 {
        background-color: rgba-to-rgb(rgba($color, 0.1))!important;
    }
    .border-#{$name}-50 {
        border-color: rgba-to-rgb(rgba($color, 0.05))!important;
    }
    .border-#{$name}-200 {
        border-color: rgba-to-rgb(rgba($color, 0.2))!important;
    }
}
.bg-light-secondary {
    background-color: #f9fafb!important;
}
// Small devices (landscape phones, 576px and up)
@media (min-width: 576px) {}

// Medium devices (tablets, 768px and up)
@media (min-width: 768px) {
    .w-md-auto {
        width: auto!important;
    }
    .w-md-25 {
        width: 25%!important;
    }
    .w-md-50 {
        width: 50%!important;
    }
    .w-md-75 {
        width: 75%!important;
    }
    .w-md-100 {
        width: 100%!important;
    }
}

// Large devices (desktops, 992px and up)
@media (min-width: 992px) {
    .w-lg-auto {
        width: auto!important;
    }
    .w-lg-25 {
        width: 25%!important;
    }
    .w-lg-50 {
        width: 50%!important;
    }
    .w-lg-75 {
        width: 75%!important;
    }
    .w-lg-100 {
        width: 100%!important;
    }
    .content-wrapper {
        margin-left: 0;
    }
    .navbar-brand {
        width: 265px!important;
        border-bottom: 0;
        flex-shrink: 0;
        display: flex!important;
    }
    .menu-active .side-nav {
        left: 0;
    }
    .menu-active .content-wrapper {
        margin-left: 265px;
    }
    .side-nav {
        top: 74px;
        left: -265px;
    }
    .toggle-menu-mobile {
        display: none!important;
    }
    .menu-active .footer {
        margin-left: 265px;
    }
}

