.select2-container {
    .select2-selection--single, .select2-selection--multiple {
        background-color: $white;
        height: auto;
        border: 1px solid #e4e6ef;
        border-radius: $border-radius;
        transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
        box-shadow: none;
        height: 40px;
    }
    .select2-selection--multiple {
        .select2-selection__rendered {
            padding: 0.35rem 1.2rem!important;
            .select2-selection__choice {
                background-color: #e5e7eb;
                border-color: transparent;
                padding: 3px 6px;
                padding-left: 22px;
                border-radius: 5px;
                color: #4b5563;
                .select2-selection__choice__remove {
                    padding: 3px 8px;
                    border-right: 0;
                    color: #4b5563;
                }
            }
        }
    }
    .select2-selection {
        .select2-selection__rendered {
            line-height: 1.5rem;
            padding: 0.608rem 1rem;
            font-size: 1rem;
            font-weight: 400;
            color: #181c32;
        }
        .select2-selection__arrow {
            padding: 1.15rem 0;
            height: 36px;
            position: absolute;
            top: 3px;
            right: 1px;
            width: 20px;
            b {
                top: 12px;
                left: 4px;
                height: 100%;
                width: 100%;
                @include arrow_down_icon(0.85rem, #6b7280);
                background-position: initial!important;
                border: none!important;
            }
        }
    }
    .select2-dropdown {
        border-radius: $border-radius;
        top: 4px;
        border: 0;
        box-shadow: 0 0 50px 0 rgb(82 63 105 / 15%);
        padding: 6px 0;
        .select2-search--dropdown {
            padding: 8px;
            padding-top: 2px;
            .select2-search__field {
                border: 1px solid #e4e6ef;
                border-radius: $border-radius;
                margin-bottom: 1px;
                padding: 0.35rem 0.5rem;
            }
        }
    }
    .select2-selection__clear {
        height: 100%!important;
        margin-right: 26px!important;
        font-size: 18px!important;
        color: #9ca3af!important;
            font-weight: 400!important;
    }
}
.select2-container--default.select2-container--open.select2-container--below .select2-selection--single, .select2-container--default.select2-container--open.select2-container--below .select2-selection--multiple {
    border-bottom-left-radius: 0.375rem;
    border-bottom-right-radius: 0.375rem;
}
.select2-container--default .select2-search--dropdown .select2-search__field {
    border-radius: $border-radius $border-radius 0 0;
    border: 0;
    border-bottom: 1px solid #e4e6ef;
    padding: .65rem 1rem;
    margin-bottom: 4px;
}
.select2-container--default.select2-container--focus .select2-selection--multiple {
    border-color: #9ca3af;
    box-shadow: none;
}
.select2-container--default .select2-search--dropdown .select2-search__field:focus {
    border-color: #9ca3af;
    box-shadow: none!important;
    outline: none;
}
.select2-container--default .select2-results>.select2-results__options {
    margin: 0;
}
.select2-results__option--selectable {
    transition: background-color .15s ease-in-out,color .15s ease-in-out;
    padding: .5rem 1rem;
}
.select2-container--default .select2-results__option--highlighted.select2-results__option--selectable:not(.select2-results__option--selected) {
    background-color: #f0f9ff;
    color: #0ea5e9;
    border-radius: 0;
    transition: background-color .15s ease-in-out,color .15s ease-in-out;
    padding: .5rem 1rem;
}
.select2-container--default .select2-results__option--selected {
    background-color: #0ea5e9;
    color: $white;
    border-radius: 0;
    transition: background-color .15s ease-in-out,color .15s ease-in-out;
    padding: .5rem 1rem;
    * {
        color: $white!important;
    }
}
.select2-container--default.select2-container--open {
    .select2-selection--single {
        border-color: #9ca3af;
        box-shadow: none;
    }
}
.select2-secondary + .select2-container {
    .select2-selection--single, .select2-selection--multiple {
        background-color: rgb(229,231,235);
    }
}
