.wrapper {
    width: 100%;
    height: 100%;
    overflow: hidden;
}
.side-nav {
    display: flex;
    flex-direction: column;
    background-color: $theme_color;
    padding: 0;
    width: 265px;
    z-index: 1012;
    bottom: 0;
    position: fixed;
    top: 0;
    left: -265px;
    padding-top: 74px;
    transition: left .3s ease;
}
.content-wrapper {
    margin-left: 0;
    overflow: hidden;
    padding: 165px 0 25px;
    min-height: 100vh;
    transition: margin-left .3s ease;
}
.navbar-head {
    display: flex;
    width: 100%;
	background-color: $white;
    min-height: 60px;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 1001;
    transition: left .3s ease;
    flex-direction: column;
    .navbar-brand {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: $theme_color;
        height: 56px;
        padding: 0 15px;
        margin: 0;
        transition: display .3s ease;
        box-shadow: 0 10px 30px 0 rgb(82 63 105 / 5%);
        .toggle-menu {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 32px;
            height: 32px;
            background-color: #fff;
            border-radius: 50%;
            box-shadow: $box-shadow-lg;
            color: #1e293b;
            border: 0;
            position: absolute;
            top: calc(50% - 16px);
            right: -16px;
            display: none;
            &:hover {
                color: #0f172a;
            }
            .minimize-active {
                display: none;
            }
            .minimize-hidden {
                display: block;
            }
        }
        .toggle-menu-mobile {
            color: #fff;
            opacity: 0.6;
            &:hover {
                opacity: 0.75;
            }
        }
        .navbar-brand-mini {
            display: block;
        }
        .navbar-brand-lg {
            display: none;
        }
    }
    .navbar-toolbar {
        display: flex;
        align-items: center;
        background-color: #fff;
        width: 100%;
        align-items: stretch;
        box-shadow: 0 10px 30px 0 rgb(82 63 105 / 5%);
        .search-btn {
            background-color: rgba(244, 244, 246, 1);
            border-color: rgba(244, 244, 246, 1);
            color: #878aa0;
            padding: 0.375rem 0.5rem;
            display: flex;
            align-items: center;
            font-weight: 400;
            &:hover {
                background-color: #e5e7eb;
                border-color:#e5e7eb;
            }
        }
        .search-icon {
            color: #a1a5b7;
        }
        .nav {
            &.navbar-top-links {
                > li:not(:last-child) {
                    margin-right: 8px;
                }
            }

            .navbar-btn {
                position: relative;
                display: flex;
                align-items: center;
                justify-content: center;
                height: 36px;
                width: 36px;
                color: #6b7280;
                background-color: transparent;
                padding: 6px;
                border-radius: $border-radius-sm;
                border: 0;
                box-shadow: none;
                .navbar-item-icon {
                    width: 18px;
                    height: 18px;
                }
                &:hover {
                    background-color: #f3f4f6;
                }
                &:focus, &:active {
                    background-color: #f3f4f6;
                }
            }
        }
    }
}
.menu-active {
    .navbar-brand {
        box-shadow: none;
    }
    .side-nav {
        left: 0;
    }
    .toggle-menu {
        .minimize-active {
            display: block!important;
        }
        .minimize-hidden {
            display: none!important;
        }
    }
}
.sidebar-toolbar {
    position: sticky;
    top: 0;
    z-index: 999;
    .sidebar-search-header {
        background-color: $theme_color;
        border-top: 1px solid $side_border_color;
        height: 1rem;
    }
    .sidebar-search-container {
        display: flex;
        align-items: center;
        padding: 0 1.65rem;
        background-color: $theme_color;
        .search-btn {
            align-items: center;
            background-color: #1e293b;
            box-shadow: inset 0 1px 0 0 hsl(0deg 0% 100% / 5%);
            color: #94a3b8;
            display: flex;
            font-weight: 400;
            padding: 0.375rem 0.5rem;
            width: 100%;
            &:hover {
                background-color: #334155!important;
                color: #94a3b8;
            }
        }
    }
    .sidebar-search-footer {
        height: 1rem;
        background-image: linear-gradient(to bottom,#0F172D,rgb(15 23 42/0));
    }
}
.side-nav {
    display: flex;
    flex-direction: column;
    background-color: $theme_color;
    padding: 0;
    &.drawer {
        width: 265px;
        left: 0;
        transform: translateX(-100%);
        display: flex!important;
        overflow: auto;
        z-index: 110;
        position: fixed;
        top: 0;
        bottom: 0;
        transition: transform .3s ease-in-out;
    }
    &.drawer-on {
        transform: none;
        box-shadow: 0 1px 9px -3px rgb(0 0 0 / 5%);
        transition: transform .3s ease-in-out;
    }
    .sidebar-scroll {
        .drawer {
            .drawer-menu {
                padding: 0 0.95rem;
                padding-bottom: 30px;
                .nav {
                    display: flex;
                    flex-wrap: wrap;
                    padding-left: 0;
                    margin-bottom: 0;
                    list-style: none;
                    flex-direction: column;
                    .drawer-menu-heading {
                        @include aside_title;
                        opacity: 0.85;
                    }
                    .nav-link {
                        color: $side_nav_link_color;
                        border-radius: 0.375rem;
                        padding: 0.75rem 0.65rem;
                        position: relative;
                        &.active {
                            color: $side_nav_active_link_color;
                            &:not(.accordion-button) {
                                background-color: $side_nav_active_link_bg_color;
                            }
                            &::before {
                                content: "";
                                position: absolute;
                                top: 9px;
                                left: -.95rem;
                                width: 0.2rem;
                                height: 1.5rem;
                                border-top-right-radius: 0.15rem;
                                border-bottom-right-radius: 0.15rem;
                                background-color: $side_nav_active_link_color;
                            }
                        }
                        &.accordion-button {
                            background-color: transparent;
                            &::after {
                                color: $side_nav_link_color;
                                @include arrow_down_icon(0.95rem, $side_nav_link_color);
                            }
                            &.active::after {
                                @include arrow_down_icon(0.95rem, $side_nav_active_link_color);
                            }
                            &:focus {
                                box-shadow: none;
                                border: 0;
                            }
                        }
                        .nav-link-icon {
                            width: 20px;
                            height: 20px;
                            font-size: 26px;
                            margin-right: 10px;
                            text-align: center;
                        }
                        .nav-link {
                            padding-left: 36px;
                            .nav-link {
                                &.before {
                                    display: none;
                                }
                            }
                        }
                        &:not(.active):hover {
                            color: #cbd5e1;
                        }
                    }
                    .accordion-collapse {
                        .nav {
                            .nav-link {
                                padding-left: 2.55rem;
                                &::before {
                                    @include aside_child_menu_dot;
                                }
                                &.active {
                                    &::before {
                                        background-color: $side_nav_active_link_color!important;
                                        @include aside_child_menu_dot;
                                    }
                                }
                                &.nav-link:not(.active):hover {
                                    color: #cbd5e1;
                                    &::before {
                                        background-color: #cbd5e1!important;
                                    }
                                }
                            }
                            .accordion-collapse {
                                .nav-link {
                                    padding-left: 3.2rem;
                                    &::before {
                                        left: 25px;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
.sidebar-footer {
    border-top: 1px solid $side_border_color;
    padding-bottom: 14px;
    padding-top: 8px;
}
.sidebar-title {
    @include aside_title;
}
.popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    overflow: hidden;
    z-index: 1009;
    background-color: rgb(0 0 0/.2);
    animation: animation-drawer-fade-in .3s ease-in-out 1;
    -webkit-backdrop-filter: blur(4px);
    backdrop-filter: blur(4px);
}
.footer {
    height: 50px;
    position: sticky;
    top: calc(100vh - 50px);
    margin-left: 0;
    z-index: 1005;
    background-color: $white;
    transition: margin-left .3s ease;
}
.profile-lg {
    border-radius: 50%;
    height: 0;
    overflow: hidden;
    padding-bottom: 100%;
    position: relative;
    background-color: #f3f4f6;
    img {
        bottom: 0;
        left: 0;
        position: absolute;
        right: 0;
        top: 0;
        width: 100%;
    }
}
.container-fluid {
    padding: 0 15px;
}
.quick-search-header {
    z-index: 1;
    padding: 0 1.25rem;
    position: relative;
    display: flex;
    flex: none;
    align-items: center;
    border-bottom-width: 1px;
    border-color: rgb(241 245 249/1);
    border-bottom-style: solid;
    .quick-search-form {
        display: flex;
        min-width: 0;
        flex: 1 1 auto;
        align-items: center;
        .quick-search-input-label {
            height: 1.6rem;
            width: 1.6rem;
            flex: none;
        }
        .quick-search-input {
            -webkit-appearance: none;
            appearance: none;
            background: transparent;
            height: 3.75rem;
            font-size: 1rem;
            color: #0f172a;
            margin-left: 0.75rem;
            margin-right: 0.6rem;
            flex: auto;
            min-width: 0;
            border: 0;
            &:focus {
                outline: none;
                box-shadow: none;
                border: 0;
            }
        }
        .quick-search-icon {
            display: block;
        }
        .quick-search-spinner {
            display: none;
        }
        &.searching {
            .quick-search-icon {
                display: none;
            }
            .quick-search-spinner {
                display: block;
            }
        }
    }
    .btn-close {
        &:focus {
            outline: none;
            box-shadow: none;
            border: 0;
        }
    }
}
.btn-toggle-screen {
    .maximize-icon {
        display: block;
    }
    .manimize-icon {
        display: none;
    }
}
.btn-toggle-screen.open {
    .maximize-icon {
        display: none;
    }
    .manimize-icon {
        display: block;
    }
}
@media (min-width: 768px) {
    .content-wrapper {
        padding-top: 120px;
    }
}
@media (min-width: 992px) {
    .navbar-head {
        min-height: 74px;
        flex-direction: row;
    }
    .navbar-brand {
        padding: 0 25px;
        min-height: 74px;
        .toggle-menu {
            display: flex!important;
        }
        .toggle-menu-mobile {
            display: none;
        }
        .navbar-brand-mini {
            display: none!important;
        }
        .navbar-brand-lg {
            display: block!important;
        }
    }
    .content-wrapper {
        padding-top: 74px;
    }
    .container-fluid {
        padding: 0 25px;
    }
}
